.slider {
  display: flex;
  flex-flow: column wrap;
  justify-content: space-between;
  height: 750px;

  &__switcher {
    display: none;

    &[id$='1']:checked ~ .slider__descriptions {
      .slider__description[class$='1']{
        z-index: 10;
      }
    }

    &[id$='1']:checked ~ .slider__images {
      .slider__image[class$='1']{
        z-index: 10;
      }
    }
    &[id$='1']:checked ~ .slider__controls {
      .slider__control {
        &--pref {
          label[for$='4']{
            display: block;
          }

          .slider__control-image--work3 {
            top: -100%;

          }

          .slider__control-image--work4 {
            top: 0;
          }

          .slider__control-image--work1 {
            top: 100%;
          }
        }

        &--next {
          label[for$='2'] {
            display: block;
          }

          .slider__control-image--work1 {
            top: -100%;
          }

          .slider__control-image--work2 {
            top: 0;
          }

          .slider__control-image--work3 {
            top: 100%;
          }
        }
      }
    }

    &[id$='2']:checked ~ .slider__descriptions {
      .slider__description[class$='2']{
        z-index: 10;
      }
    }

    &[id$='2']:checked ~ .slider__images {
      .slider__image[class$='2']{
        z-index: 10;
      }
    }
    &[id$='2']:checked ~ .slider__controls {
      .slider__control {
        &--pref {
          label[for$='1']{
            display: block;
          }

          .slider__control-image--work2 {
            top: -100%;
          }

          .slider__control-image--work1 {
            top: 0;
          }

          .slider__control-image--work3 {
            top: 100%;
          }
        }

        &--next {
          label[for$='3'] {
            display: block;
          }

          .slider__control-image--work2 {
            top: -100%;
          }

          .slider__control-image--work3 {
            top: 0;
          }

          .slider__control-image--work4 {
            top: 100%;
          }
        }
      }
    }

    &[id$='3']:checked ~ .slider__descriptions {
      .slider__description[class$='3']{
        z-index: 10;
      }
    }

    &[id$='3']:checked ~ .slider__images {
      .slider__image[class$='3']{
        z-index: 10;
      }
    }
    &[id$='3']:checked ~ .slider__controls {
      .slider__control {
        &--pref {
          label[for$='2']{
            display: block;
          }

          .slider__control-image--work3 {
            top: -100%;
          }

          .slider__control-image--work2 {
            top: 0;
          }

          .slider__control-image--work1 {
            top: 100%;
          }
        }

        &--next {
          label[for$='4'] {
            display: block;
          }

          .slider__control-image--work3 {
            top: -100%;
          }

          .slider__control-image--work4 {
            top: 0;
          }

          .slider__control-image--work1 {
            top: 100%;
          }
        }
      }
    }

    &[id$='4']:checked ~ .slider__descriptions {
      .slider__description[class$='4']{
        z-index: 10;
      }
    }

    &[id$='4']:checked ~ .slider__images {
      .slider__image[class$='4']{
        z-index: 10;
      }
    }
    &[id$='4']:checked ~ .slider__controls {
      .slider__control {
        &--pref {
          label[for$='3']{
            display: block;
          }

          .slider__control-image--work2 {
            top: -100%;
          }

          .slider__control-image--work3 {
            top: 0;
          }

          .slider__control-image--work4 {
            top: 100%;
          }
        }

        &--next {
          label[for$='1'] {
            display: block;
          }

          .slider__control-image--work2 {
            top: -100%;
          }

          .slider__control-image--work1 {
            top: 0;
          }

          .slider__control-image--work4 {
            top: 100%;
          }
        }
      }
    }
  }

  &__descriptions {
    width: 50%;
    height: 100%;
    position: relative;
    background-color: #fff;
  }

  &__images {
    width: 50%;
    height: 75%;
    position: relative;
    background-color: #fff;
  }

  &__controls {
    width: 50%;
    height: 25%;
    display: flex;
    justify-content: space-between;
  }

  &__description {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -10;
  }

  &__image {
    position: absolute;
    top: 0;
    left: 0;
    z-index: -10;
  }

  &__control {
    width: 50%;
    height: 100%;
    background-color: rgba(#b0b7aa, .7);
    position: relative;
    overflow: hidden;

    &--pref {
      label::after {
        content: '';
        @include arrow(18px, 5px, -45deg, rgba(#fff, .6), 73px)
      }
    }

    &--next {
      label::after {
        content: '';
        @include arrow(18px, 5px, 135deg, rgba(#fff, .6), 73px)
      }
    }

    &-item {
      display: none;
      cursor: pointer;
      width: 100%;
      height: 100%;
    }

    &-image {
      position: absolute;
      cursor: pointer;
      width: 100%;
      height: 100%;
      z-index: -1;
      top: -200%;
      transition-duration: 1s;
      transition-property: top;

      &--work1 {
        background: url('../image/work-1.png') no-repeat center / 280px 180px;
      }

      &--work2 {
        background: url('../image/work-2.png') no-repeat center / 280px 180px;
      }

      &--work3 {
        background: url('../image/work-3.png') no-repeat center / 280px 180px;
      }

      &--work4 {
        background: url('../image/work-2.png') no-repeat center / 280px 180px;
      }
    }
  }
}