.title {
  font-family: 'Sansus Webissimo', 'Arial', sans-serif;
  @include fonts(63px, 400, 56px, $palette-font-primary);
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  position: relative;

  &::after {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    height: 3px;
  }

  &--works {
    background: $palette-background-primary url('../image/works_header.svg') no-repeat center 0;
    background-size: 505px 165px;
    padding: 65px 0;

    &::after {
      content: '';
      width: 89px;
      bottom: 35px;
      background-color: $palette-font-primary;
      margin-left: -2px;
    }
  }
}