html, body {
  height: 100%;
}

html {
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', Arial, sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

button {
  margin: 0;
  padding: 0;
  background: none;
  border: none;
}

button:focus {
  outline: none;
}

a {
  text-decoration: none;
}

li {
  list-style: none;
}

input:focus,
textarea:focus {
  outline: none;
}

::placeholder {
  font-family: 'Roboto', 'Arial', sans-serif;
}

:focus::placeholder {
  color: transparent;
}

:focus::-webkit-input-placeholder {
  color: transparent;
}

:focus::-moz-placeholder {
  color: transparent;
}

:focus:-ms-input-placeholder {
  color: transparent;
}

.info {
  text-align: center;
}

.info__image {
  border-radius: 50%;
  width: 141px;
  overflow: hidden;
  margin: 0 auto 22px;
}

.info__image img {
  display: block;
}

.info__name {
  font-size: 35px;
  font-weight: 500;
  line-height: 41px;
  color: #fff;
  width: 500px;
  margin: 0 auto 1px;
  text-indent: 15px;
}

.info__discription {
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #fff;
  opacity: .8;
  width: 450px;
  margin: 0 auto;
  text-indent: 2px;
}

.info--index {
  margin-bottom: 32px;
}

.info--index .info__image {
  margin-bottom: 13px;
}

.info--index .info__name {
  font-size: 28px;
  line-height: 34px;
  margin-bottom: 3px;
  text-indent: -3px;
  width: 100%;
}

.info--index .info__discription {
  width: 100%;
  text-indent: 0;
}

.social-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 140px;
  font-size: 0;
}

.social-buttons--index {
  margin: 0 auto 42px;
}

.social-button {
  fill: #fff;
  opacity: .5;
}

.social-button--git {
  margin: 0 0 -2px -3px;
}

.social-button--in {
  margin-right: 1px;
}

.social-button:hover {
  fill: #497f39;
  opacity: 1;
}

.btn {
  border-radius: 0 0 5px 5px;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}

.btn__item {
  font-size: 16px;
  font-weight: 500;
  line-height: 36px;
  color: #fff;
  background-color: #608d53;
  flex-grow: 1;
  padding: 2px 0 6px;
}

.btn__item--about {
  border: 2px solid rgba(255, 255, 255, 0.5);
  border-top-width: 0;
  border-bottom-width: 0;
  padding-right: 1px;
}

.btn__item--blog {
  padding-right: 2px;
}

.btn__item:hover {
  background-color: #497f39;
}

.burger {
  margin: 18px -4px 0 0;
  width: 40px;
  height: 36px;
  cursor: pointer;
}

.burger__component {
  height: 5px;
  border-radius: 2px;
  background-color: #fff;
  box-shadow: 0 15px 0 0 #fff, 0 30px 0 0 #fff;
}

.title {
  font-family: 'Sansus Webissimo', 'Arial', sans-serif;
  font-size: 63px;
  font-weight: 400;
  line-height: 56px;
  color: #373e42;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
  position: relative;
}

.title::after {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 3px;
}

.title--works {
  background: #f8f9f5 url("../image/works_header.svg") no-repeat center 0;
  background-size: 505px 165px;
  padding: 65px 0;
}

.title--works::after {
  content: '';
  width: 89px;
  bottom: 35px;
  background-color: #373e42;
  margin-left: -2px;
}

.page-header {
  flex: 0 0 auto;
  background: #243833 url("../image/water.jpg") no-repeat center;
  background-size: 100% auto;
  padding: 15px 30px 217px;
  position: relative;
}

.page-header::before, .page-header::after {
  content: '';
}

.page-header::before {
  position: absolute;
  left: 0;
  bottom: 0;
  height: 100px;
  width: 100%;
  background: linear-gradient(to right top, #f8f9f5 50%, transparent 50%) no-repeat 0 0, linear-gradient(to left top, #f8f9f5 50%, transparent 50%) no-repeat 100% 0;
  background-size: 50% 100%;
  z-index: 10;
}

.page-header::after {
  position: absolute;
  left: 50%;
  bottom: 73px;
  width: 18px;
  height: 18px;
  border: 5px solid rgba(255, 255, 255, 0.6);
  border-top-width: 0;
  border-right-width: 0;
  transform: translateX(-50%) rotate(-45deg);
  cursor: pointer;
  z-index: 10;
}

.page-header__menu {
  display: flex;
  justify-content: space-between;
  margin-bottom: 84px;
}

.index__wrapper {
  height: 100%;
  min-height: 700px;
  background: #243833 url("../image/water.jpg") no-repeat center;
  background-size: auto 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.index__header {
  flex: 0 0 auto;
  padding-top: 20px;
  align-self: flex-end;
  margin-bottom: 55px;
}

.index__authorization {
  font-size: 16px;
  font-weight: 500;
  line-height: 37px;
  color: #fff;
  display: block;
  border: 1px solid #fff;
  border-radius: 50px;
  margin-right: 19px;
  padding: 1px 27px 5px;
}

.index__authorization:hover {
  border-color: #729e19;
  background-color: #4e8839;
}

.page-main {
  flex: 1 0 auto;
}

.welcome {
  padding-top: 40px;
  text-align: center;
  width: 380px;
  background-color: rgba(18, 38, 32, 0.6);
  border-radius: 5px;
}

.authorization {
  display: none;
}

