  width: 50%;
  height: 100%;
  background-color: rgba(176, 183, 170, 0.7);
  position: relative;
  overflow: hidden;
}

.slider__control--pref label::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 73px;
  width: 18px;
  height: 18px;
  border: 5px solid rgba(255, 255, 255, 0.6);
  border-top-width: 0;
  border-right-width: 0;
  transform: translateX(-50%) rotate(-45deg);
  cursor: pointer;
  z-index: 10;
}

.slider__control--next label::after {
  content: '';
  position: absolute;
  left: 50%;
  bottom: 73px;
  width: 18px;
  height: 18px;
  border: 5px solid rgba(255, 255, 255, 0.6);
  border-top-width: 0;
  border-right-width: 0;
  transform: translateX(-50%) rotate(135deg);
  cursor: pointer;
  z-index: 10;
}

.slider__control-item {
  display: none;
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.slider__control-image {
  position: absolute;
  cursor: pointer;
  width: 100%;
  height: 100%;
  z-index: -1;
  top: -200%;
  transition-duration: 1s;
  transition-property: top;
}

.slider__control-image--work1 {
  background: url("../image/work-1.png") no-repeat center/280px 180px;
}

.slider__control-image--work2 {
  background: url("../image/work-2.png") no-repeat center/280px 180px;
}

.slider__control-image--work3 {
  background: url("../image/work-3.png") no-repeat center/280px 180px;
}

.slider__control-image--work4 {
  background: url("../image/work-2.png") no-repeat center/280px 180px;
}

